
.header{
    display: block;
    width: 100%;
    height: 100%;
    padding-bottom: 265px;
}
.bg--gray{
	background: #89898b;
}
.header__container{
	width: 642px;
	margin: 0 auto;
}
.header__logo{
	padding: 165px 276px 50px;
}
.header__title{
	text-align: center;
	padding-bottom: 50px;
	color: #fff;
	font-family: 'open_sanslight', sans-serif;
}
.header__main--title{
    text-align: center;
    font-size: 37px;
    color: #ffe600;
    padding-bottom: 110px;
    font-family: 'open_sanslight', sans-serif;
}
.header__btn{
	padding: 23px 75px;
	border: 1px solid #ffe600;
	color: #fff;
	background: transparent;
	display: block;
	margin: 0 auto;
	cursor: pointer;
	font-family: 'open_sanslight', sans-serif;
}
.header__btn:hover{
	display: block;
	background: #ffe600;
	transition: 0.3s;
	opacity: 0.8; 
}

